.header {
	@include reset;
	@include flexbox(true);
	flex-direction: column;

	width: 100vw;
	min-height: 120vh;
	height: auto;

	background-color: var(--primary);
	color: #ffffff;

	padding-top: 10vh;
	padding-bottom: 10vh;

	transition: 0.4s cubic-bezier(0.23, 1, 0.320, 1);

	&__content {
		@include flexbox(true);
		flex-direction: column;
	}

	&__heading {
		text-align: center;
		margin-top: 50px;
		font-size: 6.4rem;
	}

	&__sub-heading {
		margin-top: 20px;
		font-family: 'Interface';
		font-weight: 400;
		font-size: 2.4rem;
		width: 50vw;
		text-align: center;
		color: #ffdceb;
	}

	&__image {
		width: 240px;
		height: 240px;
	}

	// &__model {
	// 	width: 80%;
	// 	height: 64vh;
	// }
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}
