$font-path: '../assets/fonts/Interface/';
$font-path-proxima: '../../assets/fonts/ProximaNova';

@font-face {
    font-family: 'Interface';
    src: url('#{$font-path}Interface-Regular.ttf');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Interface Black';
    src: url('#{$font-path}Interface-Black.ttf');
}

@font-face {
    font-family: 'Interface';
    src: url('#{$font-path}Interface-Bold.ttf');
    font-weight: bold;
    font-style: normal;
}

@font-face {
    font-family: 'Interface';
    src: url('#{$font-path}Interface-Medium.ttf');
    font-weight: medium;
    font-style: normal;
}

