.hamburger {
	position: fixed;
	top: 45px;
	left: 45px;
	z-index: 6;

	.burger {
		width: 32px;
		height: 2px;
		background-color: #e2e2e2;
		transition: 0.4s cubic-bezier(1,0,0,1);

		div {
			width: 0;
			height: 100%;
			background-color: #fff;
			transition: 0.6s cubic-bezier(1, 0, 0, 1);
		}

		&.close {
			background-color: #000000;
			transform: rotate(45deg) translateY(7px);

			div {
				background-color: #555555;
			}
		}

		&:last-child {
			margin-top: 8px;

			div {
				transition-delay: 0.3s;
			}

			&.close {
				transform: rotate(-45deg) translateY(-7px);
			}
		}
	}

	&:hover {
		.burger div {
			width: 100%;
		}
	}
}

.nav {
	@include reset;
	@include flexbox(true);
	position: fixed;
	transform: translateX(-100vw);
	width: 100vw;
	height: 100vh;
	z-index: 5;
	background-color: #fff;
	transition: 0.4s 0.3s cubic-bezier(1,0,0,1);

	&.open {
		transform: translateX(0);
	}

	&__left {
		width: 100%;
		height: 100vh;
		@include flexbox(true);
		flex-direction: column;
	}

	&__link {
		display: inline-block;
		font-size: 4.8rem;
		font-weight: 600;
		opacity: 0;
		transition: 0.4s;
		color: #000000;
		margin: -10px;

		&.fade-in-left {
			transition: 0.4s;
			animation: fadeInLeft 0.4s 0.7s forwards;

			&:nth-child(2) {
				animation-delay: 0.8s;
			}

			&:nth-child(3) {
				animation-delay: 0.9s;
			}
		}

		&::after {
			content: '';
			position: relative;
			display: block;
			top: -45px;
			width: 0;
			height: 45px;

			background-color: var(--hover-color);
			opacity: 0.5;

			transition: 0.4s cubic-bezier(1,0,0,1);
		}

		&:hover {
			&::after {
				width: 100%;
			}
		}
	}
}

@keyframes fadeInLeft {
	from {
		opacity: 0;
		transform: translateX(-60px);
	}

	to {
		opacity: 1;
		transform: translateX(0);
	}
}

.sweeper {
	@include reset;
	@include flexbox(true);
	position: fixed;
	transform: translateX(-100vw);
	width: 100vw;
	height: 100vh;
	z-index: 4;
	background-color: #da1525;

	transition: 0.4s cubic-bezier(1, 0, 0, 1);

	&.open {
		transform: translateX(0);
	}
}
