.sniper {
	background-color: var(--primary);
	display: flex;

	[class*=col] {
		@include flexbox(true);
		height: 100vh;

		&.right {
			padding-left: 5vw;
			align-items: flex-start;
			flex-direction: column;
		}
	}

	&__image {
		width: 75%;
		height: 480px;
		transform: scale(0.7);
		transition: 0.4s cubic-bezier(0.23, 1, 0.320, 1);
	}

	&__heading {
		font-size: 6.4rem;
		color: #ffffff;
		margin-bottom: 20px;
	}

	&__description {
		font-family: 'Interface';
		color: #dddddd;
		font-size: 2rem;
		width: 90%;
	}
}
