.shield {
	min-height: 100vh;
	height: auto;
	display: flex;
	justify-content: center;
	background-color: darken($primary, 1%);
	color: #68686C;

	&__content {
		padding-left: 5vw;
		width: 57vw;
		height: 100vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	&__heading {
		font-size: 6.4rem;
		color: #ffffff;
	}

	&__sub-heading {
		font-family: 'Interface';
		color: #d6d6d6;
		font-size: 2.8rem;
	}

	&__description {
		font-family: 'Interface';
		color: #e7e7e7;
		font-size: 2rem;
		width: 90%;
	}

	&__button {
		margin-top: 60px;
		@include flexbox(true);
		width: 160px;
		height: 60px;
		background-color: var(--btn-color);
		color: #fff;
		border-radius: 10px;
		font-size: 1.8rem;
		font-weight: 600;
		transition: 0.2s ease-in;

		&:hover {
			transform: scale(1.05) translateY(-5px);
			box-shadow: 2px 2px 5px rgba(#ffdceb, 0.2);
		}
	}

	&__model {
		@include flexbox(true);
		width: 43vw;
		height: 100%;
	}

	&__image {
		width: 48%;
		height: 92vh;
		margin-top: 16px;
		margin-left: -20px;
		transform: scale(0.6);
		transition: 0.4s cubic-bezier(0.23, 1, 0.320, 1);
	}

	ul {
		font-size: 1.6rem;

		font-family: 'Interface';
		margin-bottom: -20px;

		color: #ffffff;
	}
}

.shield--stats {
	min-height: 90vh;
	height: auto;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: darken($primary, 1%);
	color: #ffffff;

	.stat-box {
		padding:0 50px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 80vw;
		margin-top: -20px;
		height: 64vh;
		background-color: darken($primary, 1.5%);

		box-shadow: 15px 15px 20px rgba(#000000, 0.4);

		transition: 0.2s ease-in;

		&:hover {
			$scale: 1.02;
			transform: scale3d($scale,$scale,$scale);
		}
	}

	.shield__stat {
		display: flex;
		align-items: center;
		width: 100%;
		height: 100%;

		.left-stats {
			width: 70%;
		}

		.right-number {
			@include flexbox(true);
			flex-direction: column;
			width: 30%;
			height: 100%;
			h1 {
				background-image: linear-gradient(135deg, #5EFCE8 10%, #736EFE 100%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent;
				font-size: 5.4rem;
				margin: -10px;
			}
		}

		.shield__progress-bar {
			border-radius: 5px;
			width: 80%;
			height: 6px;
			margin-top: 20px;
		}

		.shield__compare-bar {
			border-radius: 5px;
			height: 6px;
			width: 64%;
			background-image: linear-gradient(to right, #f7f7f7 0%, #e9e9e9 47%, #bdbdbd 100%);
		}

		&--strength {

			.shield__progress-bar {
				background-image: linear-gradient(135deg, #5EFCE8 10%, #736EFE 100%);
			}
		}

		&--attack {
			.shield__progress-bar {
				background-image: linear-gradient(135deg, #FCCF31 10%, #F55555 100%);
			}

			.shield__compare-bar {
				width: 52%;
			}

			.right-number h1 {
				background-image: linear-gradient(135deg, #FCCF31 10%, #F55555 100%);
			}
		}

		&-heading {
			font-size: 2.4rem;
		}

		&-spec {
			margin-top: 5px;
			margin-bottom: 20px;
			color: #686868;
			font-family: 'Interface';
			font-size: 1.38rem;
			font-weight: 400;

			&--large {
				font-size: 1.46rem;
			}
		}
	}
}
