@import 'utilities';
@import 'flex-grid';
@import 'fonts';
@import 'colors';
*,
*::before,
*::after {
    @include boxSizing;
    transition: all 700ms ease-in;
}

html {
    @include overflowHack;
    @include boxSizing;
    font-size: 10px;
}

body {
    @include reset;
    @include boxSizing;
    min-width: 100vw;
    @include overflowHack;
    font-size: 10px;
    background-color: #fff;
    font-family: 'Saira', 'Interface', $fallback--fonts;
    overflow-x: hidden;
}

::-webkit-scrollbar {
}

::-webkit-scrollbar-thumb {
}

h1,
h2,
h3,
h4,
h5,
h6 {
    @include reset;
}

.section {
    @include reset;
    @include overflowHack;
    width: 100vw;
    height: 100vh;
}

a {
    text-decoration: none;
}

.wrapper {
    @include overflowHack;
    min-height: 100vh;
    width: 100vw;
    transition: 0.5s;
}
