.wand {
	height: auto;
	display: flex;
	justify-content: center;
	flex-direction: column;
	background-color: var(--primary);

	transition: all 0.4s ease-in-out;

	padding-top: 10vh;

	&__heading {
		margin-left: 8vw;
		font-size: 5.6rem;
		width: 70vw;
		font-family: 'Interface';
		color: #ffe9fb;


		span {
			background-image: linear-gradient(135deg, #F97794 10%, #623AA2 100%);
			background-image: linear-gradient(135deg, rgba(#F97794, 0.3) 10%, rgba(#623AA2, 0.3) 100%),
			url('https://source.unsplash.com/5DIFvVwe6wk');
			// background-image: linear-gradient(135deg, rgba(#F97794, 0.3) 10%, rgba(#623AA2, 0.3) 100%),	url('https://source.unsplash.com/bK8Wlaq7NaA');
			background-position: center center;
			background-size: cover;
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}

	&__description {
		width: 100%;
		min-height: 100vh;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	&__image {
		margin-top: 10vh;
		// width: 54vw;
		height: 60vh;
		transform: scale(0.7) rotate(0deg);
		transition: 0.4s cubic-bezier(0.23, 1, 0.320, 1);
	}

	&__features {
		margin-top: -5vh;
		padding: 60px;
		width: 100vw;
		@include flexbox(true);
		transition: 0.4s cubic-bezier(0.23, 1, 0.320, 1);
		opacity: 0;
	}

	.feature {
		font-family: 'Interface';
		--title-color: #fff;
		--text-color: #dadada;

		&__title {
			font-size: 2.4rem;
			color: var(--title-color);
			font-weight: 500;
		}

		&__text {
			font-weight: 400;
			font-size: 1.6rem;
			width: 90%;
			color: var(--text-color);

			&:not(:last-child) {
				margin-bottom: 40px;
			}
		}
	}
}
