.arena {
	@include flexbox(true);
	min-height: 100vh;
	height: auto;
	flex-direction: column;
	justify-content: flex-start;
	padding: 8vh 5vw;
	background-color: darken(#121217, 1%);

	&__heading {
		color: #ffffff;
		font-size: 6.4rem;
	}

	&__sub-heading {
		color: #d1d1d1;
		font-size: 2rem;
		font-family: 'Interface';
		text-align: center;
		font-weight: 400;
		width: 80%;
		margin-top: 20px;
		margin-bottom: 80px;
	}

	&__model {
		width: 80%;
		height: 110vh;
	}
}
