.btn {
	margin-top: 60px;
	@include flexbox(true);
	width: 160px;
	height: 60px;
	background-color: var(--btn-color);
	color: #fff;
	border-radius: 10px;
	font-size: 1.8rem;
	font-weight: 600;
	transition: 0.2s ease-in;

	&--left-margin {
		margin-left: 30px;
	}

	&:hover {
		transform: scale(1.05) translateY(-5px);
		box-shadow: 2px 2px 5px rgba(#ffdceb, 0.2);
	}
}
