/*----------- Maps -----------*/

$z-index: (
    'main-heading': '2',
    'hamburger': '4',
    'sidebar': '3',
);

$breakpoints: (
    'sm': 'screen and (max-width: 649px)',
    'md': 'screen and (max-width: 999px) and (min-width: 650px)',
    'lg': 'screen and (min-width: 1000px)',
    'nexus5': 'screen and (max-height: 560px)',
    'iphone4': 'screen and (max-height: 480px)',
);

$sm: map-get($breakpoints,sm);
$sm-ev: 'screen and (max-width: 768px)';
$md: map-get($breakpoints,md);
$lg: map-get($breakpoints,lg);
$iphone4: map-get($breakpoints,iphone4);
$nexus5: map-get($breakpoints,nexus5);

/* Font Defaults */

$fallback--fonts: 'Interface', Helvetica, Verdana, Arial, sans-serif;

/*----------- Mixins -----------*/

@mixin reset($m: true, $p: true) {
    @if $m !=false {
        @if $m==true {
            margin: 0;
        }
        @else {
            margin: $m;
        }
    }
    @if $p !=false {
        @if $p==true {
            padding: 0;
        }
        @else {
            padding: $p;
        }
    }
}

@mixin boxSizing {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -ms-box-sizing: border-box;
    box-sizing: border-box;
}

@mixin flexbox($center: false) {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    @if ($center) {
        align-items: center;
        justify-content: center;
    }
}

@mixin vCenter($pos: absolute, $t: 50vh) {
    position: $pos;
    top: $t;
    transform: translateY(-50%);
}

@mixin btn($color: #fff) {
    text-decoration: none;
    border: 2px solid $color;
    border-radius: 4px;
    padding: 1rem 2.4rem;
    font-size: 1.75rem;
    color: $color;
}

@mixin square($d) {
    width: $d;
    height: $d;
}

@mixin background($attachment: fixed,$position: center, $size: cover,$repeat: no-repeat) {
    background-attachment: $attachment;
    background-size: $size;
    background-repeat: $repeat;
    background-position: center $position;
}

@mixin respond-to($breakpoint) {
    @if $breakpoint=="small" {
        @media screen and (max-width: 649px) {
            @content;
        }
    }
    @else if $breakpoint=="medium" {
        @media screen and (max-width: 999px){
            @content;
        }
    }
    @else if $breakpoint=="large" {
        @media screen and (min-width: 1000px) {
            @content;
        }
    }
}

@mixin font($size: 1.7rem,$weight: 400,$serif:false,$family...) {
    font-size: $size;
    font-weight: 400;
    font-family: $family, if($serif,serif,sans-serif);
}

@function font($font-src,$extension: ttf) {
    @return url(../fonts/#{$font-src}.#{$extension});
}

@function image($img-src,$extension) {
    @return url(../images/#{$img-src}.#{$extension});
}

@mixin padding($top,$bottom,$left,$right,$v-set: true,$h-set: true) {
    @if ($v-set and $h-set) {
        padding-top: $top;
        padding-bottom: $top;
        padding-left: $left;
        padding-right: $right;
    } @else {
        padding: {
            top: $top;
            bottom: $bottom;
            left: $left;
            right: $right;
        }
    }
}

@mixin overflowHack {
    max-width: 100%;
}

@mixin placeholder {
    ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        @content;
    }
    ::-moz-placeholder { /* Firefox 19+ */
        @content;
    }
    :-ms-input-placeholder { /* IE 10+ */
        @content;
    }
    :-moz-placeholder { /* Firefox 18- */
        @content;
    }
    ::-input-placeholder {
        @content;
    }
}

@mixin selection($background, $color) {
    ::-moz-selection {
        background-color: $background;
        color: $color;
    }

    ::selection {
        background-color: $background;
        color: $color;
    }
}
